<template>
    <section :class="{ 'main-content': true, 'no-left-bar': catalogId != '' && catalogId != null }">
        <!-- start page heading -->
        <loader :is-visible="isLoading"></loader>  
        <loader :is-visible="isLoadings"></loader>  
        <section class="page-action">
            <div class="" v-if="catalogId != '' && catalogId != null">
                <div class="row">
                    <div class="col-lg-12 mt-2">
                        <div class="heading-border mb-2">
                            <h5 class="h5">
                                <span>{{$t('RelatedInformation')}}</span>
                                <small class="float-right mt-2" v-if="RelatedInformationData.length==0">
                                    <a id="DisablebtnBarCode" href="javascript:void(0);" title="Download Barcode" 
                                        class="disabled pr-2"><i class="fa fa-barcode pr-2"></i>Barcode</a>
                                    <a id="DisablebtnBarCodePDF" href="javascript:void(0);" title="Barcode Print"
                                        class="barCodePDF "><i class="fa fa-print pr-2"></i>Barcode Print</a>
                                        </small>
                                        <small class="float-right mt-2"  v-if="RelatedInformationData.length!=0">
                                    <a id="btnBarCode" class="mr-2" v-on:click="DownloadAllBarcode" href="javascript:void(0);" title="Download Barcode"><i
                                            class="fa fa-barcode pr-2"></i>Barcode</a>
                                    <a id="btnBarCodePDF" href="javascript:void(0);" title="Barcode Print"
                                        class="barCodePDF" v-on:click="DownloadAllBarcodePrint" ><i class="fa fa-print pr-2"></i>Barcode Print</a>

                                </small>
                            </h5>
                        </div>
                    </div>
                </div>
            </div>

            <!-- end page heading -->
            <div class=" d-flex flex-wrap py-2">
                <div class="right-content" v-if="catalogId != '' && catalogId != null">
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 Search-panel float-left p-0">
                        <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
                            <div class="row mt-0">
                                <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pr-1 pl-0">
                                    <div class="form-group mb-0">
                                        <input id="relatedAssetSearch" v-model="txtUniqueNameCode" class="form-control" type="text"
                                            placeholder="Search by Unique Name/Product Code" />
                                    </div>
                                </div>
                                <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pl-0">
                                    <div class="search-btm-btn background-0 text-left form-check-inline">
                                        <a id="aRelatedSearchAsset" v-on:click="SearchByuniqueNameCode" title="Search"
                                            class="btn btn-success float-left"><i class="fa fa-search"></i></a>
                                        <a id="aUN_ClearSearch"  v-on:click="ClearSearch" class="btn btn-danger ml-1 float-left"
                                            title="Clear"><i class="fa fa-refresh"></i></a>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>

                <tg-list :showCheckBox="true" 
                         :IsShowAction="false" 
                         :listType="['List']" 
                         ModuleName="Asset"
                         SubModuleCode="Asset" 
                         IdentityColumn="RelatedInfoId" 
                         :PageSize="PageSize"                    
                         :HeaderText="$t('AssetManagement')"
                         :widgets="widgets"
                        :ListData="RelatedInformationData" 
                        :HeaderData="Headers"                       
                        :callbackfunction="FetchData" 
                        :LegendArray="LegendArray"
                        :SortExp="SortExp"
                        :SortBy="SortBy"
                        :NorecordfoundText="$t('NoRecordfound')"
                        :HeaderButtons="listheaderbuttons" 
                        :TotalRecords="TotalRecords" 
                        :SearchFields="leftSearchFields"
                        :RenderRowActionMethod="renderActions" 
                        :ListDataCallBackFunction="FetchData"
                        v-on:LeftsearchButtonClick="leftCommonSearch"
                        v-on:PagerButtonClick="pagerMethod" 
                        v-on:HeaderButtonClick="commonHeaderButtonClick"
                        v-on:SortdataButtonClick="sortdata"
                        >
                    <template v-slot:slotdata="{ data }">
                        <template v-if="data.column.COLUMN_NAME == 'UniqueName'">
                            <span class="text-dark" :title="data.row.UNIQUE_NAME">
                                <em class="text-primary"  style="cursor:pointer;"  v-on:click="UniqueNameclick(data.row.RelatedInfoId)">{{
                                    data.row.UniqueName }}</em>
                            </span>
                        </template>
                        <template v-if="data.column.COLUMN_NAME == 'TotalItems'">
                            <span class="text-dark" :title="data.row.QUANTITY">
                                <i class="fa fa-list" aria-hidden="true"></i>
                                <span class="noti-circle noti-blue">{{ data.row.TotalItems > 0 ? data.row.TotalItems : 0
                                }}</span>
                            </span>
                        </template>
                        <template v-if="data.column.COLUMN_NAME == 'BarcodeImageUrl'">
                            <div class="input-group mb-3 flex-wrap" title=""  style="min-width: 360px;">
                                <div class="text-center pt-0 pb-2 w-100" title="">{{ data.row.UniqueName }}</div>
                                <div class="form-control p-0 border-0 text-center">
                                    <img :src='data.row.BarcodeImageUrl' title="" style="max-height:40px;" />
                                    <span class="d-block w-100 text-center">{{ data.row.BARCODE_NUMBER }}</span>
                                    <div class="text-center pt-0 pb-2 w-100" title="">{{ data.row.BarcodeNumber }}</div>
                                </div>
                                <div class="input-group-append" style="font-size: 50px;">
                                    <span id="basic-addon2" title=""
                                        style="width: 40px;height: 40px;border-radius: 100%;padding: 0px;text-align: center;display: flex;justify-content: center; cursor: pointer;"
                                        class=" mx-1 input-group-text border-0 bg-info">
                                        <a style="font-size:19px;" :catalogId="data.row.RelatedInfoId" @click="DownloadBarcode(data.row.RelatedInfoId)" title="Download Barcode">
                                            <i class="fa fa-download text-white action-icon enable"></i>
                                        </a>
                                    </span>
                                </div>
                            </div>
                        </template>
                        <template v-if="data.column.COLUMN_NAME == 'STATUS_CODE'">
                            <a class="inactive-disabled">
                                <span class="btn d-inline-block text-center text-truncate btn-success">
                                    {{ data.row[data.column.COLUMN_NAME] }}
                                </span>
                            </a>
                        </template>
                        <template v-if="data.column.COLUMN_NAME == 'CalibrationDate'">
                            <a  class="table-head-btn viewcomment" @click="AuditHistoryClick(data.row,'Calibration')"  :id="data.row.CalibrationDate">
                                <em class="fa fa-comment text-info action-icon center-icon">
                                    <sup> {{ data.row.CalibrateHistories }} </sup>  
                                </em>                               
                            </a>
                            <span> {{ data.row.CalibrationDate }}</span>
                        </template>
                        <template v-if="data.column.COLUMN_NAME == 'LastAuditDate'">
                             <a  class="table-head-btn viewcomment" @click="AuditHistoryClick(data.row,'Audit')"  :id="data.row.RelatedInfoId" >
                                <em class="fa fa-comment text-info action-icon center-icon">                                   
                                    <sup> {{ data.row.AuditHistories }} </sup>                                      
                                </em>
                            </a>
                            <span> {{ data.row.LastAuditDate }}</span>
                        </template>
                        <template v-if="data.column.COLUMN_NAME == 'AvailableItems'">
                            <span class="text-dark" :title="data.row.QUANTITY">
                                <i class="fa fa-list" aria-hidden="true"></i>
                                <span class="noti-circle noti-blue">{{ data.row.AvailableItems > 0 ? data.row.AvailableItems : 0
                                }}</span>
                            </span>
                           
                        </template>
                        <template v-if="data.column.COLUMN_NAME == 'Calendar'">
                            <a title="Calendar"  rid='RelatedInfoId' style="cursor:pointer;" value=""   @click="ShowCalender(data.row)">
                               <em class="fa fa-calendar text-primary action-icon center-icon"></em>
                            </a>
                        </template>                       
                        <template v-if="data.column.COLUMN_NAME == 'HISTORY'">
                            <em title="View History" data-placement="left" :data-barcode="data.row.BARCODE_NUMBER" :data-serialnumber="data.row.SERIAL_NUMBER" :data-uniqueName="data.row.UNIQUE_NAME"
                                class="updateBarcode"  v-on:click="ViewHistory(data.row)" style="cursor:pointer;" value="">
                                <i class="fa fa-file text-warning action-icon center-icon"></i>
                        </em>
                        </template>
                        <template v-if="data.column.COLUMN_NAME == 'FileType'" title="" id="Removespan">
                            <span id="basic-addon2"  v-if="data.row.FileType == null" title=""   style="width:40px;cursor: pointer;height: 40px;border-radius: 100%;padding: 0px;text-align: center;display: flex;justify-content: center;" class="center-icon mx-1 input-group-text border-0 bg-secondary">
                                <em  class="downloadfilenofile disabled" title="" >
                                    <i class="fa fa-download text-white action-icon disable center-icon"  title="Download Attachment"></i>
                                </em>
                            </span>
                            <span v-else id="basic-addon2"  title="Download Attachment"  style="width: 40px;height: 40px;border-radius: 100%;padding: 0px;text-align: center;display: flex;justify-content: center;  cursor: pointer;" class="center-icon mx-1 input-group-text border-0 bg-info">
                                <em  class="downloadfile" title="Download Attachment"    v-on:click="DownLoadFile(data.row)">
                                    <i class="fa fa-download text-white action-icon enable center-icon"  title="Download Attachment"></i>
                                </em>
                            </span>
                        </template>
                        <template v-if="data.column.COLUMN_NAME == 'Action'">
                            <em title="Edit" data-placement="left" :data-barcode="data.row.BARCODE_NUMBER" v-on:click="UpdateBarcode(data.row)"
                                :data-serialnumber="data.row.SERIAL_NUMBER" :data-uniqueName="data.row.UNIQUE_NAME" class="updateBarcode"   style="cursor:pointer;" value="">
                                <i class="fa fa-pencil text-primary action-icon center-icon"></i>
                        </em>
                        </template>
                        <template v-if="data.column.COLUMN_NAME == 'StatusName'">
                                <td class="text-center single-action">
                                    <span class="cstm-drop-btn">
                                        <select name="Reader"  v-model="data.row.StatusId" v-on:change="changeStatus(data.row.RelatedInfoId,data.row.StatusId)" :class="{'ddlupdatestatus w-80 btn-success' : data.row.StatusName=='ACT', 'ddlupdatestatus w-80 btn-danger' : data.row.StatusName=='INACT' ,
                                         'float-left w-100 btn-info valid' : data.row.StatusName=='CATALOGUE_STOCK', 'UpdateItemStatus float-left  btn-warning' : data.row.StatusName=='CATALOGUE_USE' , 'UpdateItemStatus float-left w-100 btn-primary' : data.row.StatusName=='ASSET_TRANSIT', 'UpdateItemStatus float-left w-100 btn-danger valid' : data.row.StatusName=='CATALOGUE_RETIRED'}">
                                       
                                        <option v-for="(item, itemIndex) in StatusListData" :key="itemIndex" :value="item.STATUS_ID">{{ item.STATUS }}</option>
                                    </select>  
                                    </span>
                                </td>
                            </template>
                    </template>

                </tg-list>
            </div> &nbsp;
            <div class="p-3">
        <dynamic-form lang="en" v-if="chkRelatedInfo" :buttons="buttons"></dynamic-form>
    </div> 
            <RelatedInformationModel v-if="IscheckInfromation" @ClosePOPuP="ClosePOPuP" :RelatedInfoId="RelatedInfoId"></RelatedInformationModel>   
            <DeployProductpopup v-if="IscheckDeployProduct" @ClosePOp="ClosePOp"  :RelatedInfoId="RelatedInfoId"> </DeployProductpopup>
            <UpdateBarCodeModel v-if="IsUpdateBarCode" @ClosePOP="ClosePOP" :BarcodeArray="BarcodeArray"> </UpdateBarCodeModel>
            <AddProduct v-if="ShowAddProduct" :FromPage="FromPage"></AddProduct>
            <Calendar v-if="Calenderpopup" @CloseCalendarpopup="CloseCalendarpopup"></Calendar>
            <AuditHistoryModel v-if="isHistoryModel" :TabName="TabName" :HistoryItems="HistoryItems" @CloseAuditpopup="CloseAuditpopup"></AuditHistoryModel>
            <BarcodePrintPopup v-if="isShowBarcodePrintPopup" :htmlsrc="htmlsrc"></BarcodePrintPopup>
        </section>
        <!--LIST TOGGLE FULL SLIDE BUTTON-->
    </section>
</template>
<script>
import axios from 'axios';
import { useloggedUserInfo } from "../../stores/UserInfo";  
import DataService from '../../service/DataService';
import Schema from "../AssetCatalog/RelatedInformationSchema";
import DeployProductpopup from '../../components/Modal/DeployProductpopup.vue';
import RelatedInformationModel from '../../components/Modal/RelatedInformationPopup.vue';
import UpdateBarCodeModel from '../../components/Modal/UpdateBarCode.vue';
import StatusList from '../../components/Common/ListingStatus.vue'
import AddProduct from '../../components/Modal/AddProduct.vue'
import Calendar from '../../components/Modal/Calendar.vue'
import AuditHistoryModel from '../../components/Modal/AuditHistory.vue'
import BarcodePrintPopup from '../../components/Modal/BarcodePrintPopup.vue';
export default {
    props:{
        chkRelatedInfo:{
            type:Boolean
        }
    },
    components: {
        RelatedInformationModel,
        DeployProductpopup,
        StatusList,
        UpdateBarCodeModel,
        AddProduct,
        Calendar,
        AuditHistoryModel,
        BarcodePrintPopup
    },
    data() {
         return {
            RelatedInformationData: [],                          
            BarcodeArray:[],    
            Headers: [
                {
                "COLUMN_NAME": "LocationName",
                "DATA_TYPE": "image",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME":  this.$t('Location'),
                "DISPLAY_ORDER": 1,
            },
            {
                "COLUMN_NAME": "UniqueName",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('UniqueNameProductCode'),
                "DISPLAY_ORDER": 1,
                settings: {
                    isInSlot: true
                }
            },
            
            {
                "COLUMN_NAME": "TotalItems",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('TotalItems'),
                "DISPLAY_ORDER": 3,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "AvailableItems",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('AvailableQuantity'),
                "DISPLAY_ORDER": 4,
                settings: {
                    isInSlot: true
                }

            },
            {
                "COLUMN_NAME": "BarcodeImageUrl",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Barcode'),
                "DISPLAY_ORDER": 9,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "SerialNumber",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('SerialNumber'),
                "DISPLAY_ORDER": 10,

            },
            {
                "COLUMN_NAME": "Calendar",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Calendar'),
                "DISPLAY_ORDER": 5,
                settings: {
                    isInSlot: true
                }

            },
            {
                "COLUMN_NAME": "phonenumber",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('PHONE_NUMBER'),
                "DISPLAY_ORDER": 11,
            },
            {
                "COLUMN_NAME": "Cost",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Cost'),
                "DISPLAY_ORDER": 12,
            },
            {
                "COLUMN_NAME": "WarrantyExpirationDate",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('WarrantyExpirationDate'),
                "DISPLAY_ORDER": 13,

            },
            {
                "COLUMN_NAME": "CalibrationDate",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('CalibrationDate'),
                "DISPLAY_ORDER": 14,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "LastAuditDate",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('AuditDate'),
                "DISPLAY_ORDER": 15,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "FileType",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('ATTACHMENT_PATH'),
                "DISPLAY_ORDER": 16,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "StatusName",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('STATUS_NAME'),
                "DISPLAY_ORDER": 8,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "HISTORY",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('ViewHistory'),
                "DISPLAY_ORDER": 17,
                settings: {
                    isInSlot: true
                }
            },
            
            {
                "COLUMN_NAME": "Action",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('UpdateBarcode'),
                "DISPLAY_ORDER": 18,
                settings: {
                    isInSlot: true
                }

            }

            ],
            DeployItemData: [],
            PageSize: 10,
            PageNumber: 1,
            PageSizeOption: null,
            noRecord: true,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SortBy: '',
            SortExp: '',
            isLoading:false,
            isLoadings:false,
            IscheckInfromation: false,
            IscheckDeployProduct: false,
            IsUpdateBarCode: false,
            isHistoryModel: false,
            catalogId: '',
            RelatedInfoId: '',
            leftSearchFields: Schema.leftsearchSchema,
            listheaderbuttons: Schema.buttons,
            widgets:[],
            LegendArray:[],
            ShowAddProduct:false,
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-warning HideTab",
                    text: "Previous <i class='fa fa-angle-double-left pr-2'></i>",
                    isVisible: true,  
                    onClick: this.onPreviousClick
                },
                {
                    type: "submit",
                    class: "btn btn-primary HideTab",
                    text: "Next<i class='fa fa-angle-double-right pl-2'></i>",
                    isVisible: true,
                    onClick: this.onNextClick
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save ",
                    isVisible: false,
                    onClick:this.onSubmit
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    isVisible: false,
                    onClick: this.onCancel
                }
            ],
            InfoData:[],                        
            pdfData:[],                        
            FileData:[] ,
            HistoryItems:[],
            Calenderpopup:false,            
            txtUniqueNameCode:'',
            searchBy:'',
            StatusListData:[],
            privilegeParams:null,
            isShowBarcodePrintPopup:false,
            htmlsrc:""
        }
    },
   async created() {
   
        var vm = this;
        await vm.GetPageSize();
        vm.catalogId = ((window.location.href.split('/')[5]) === undefined) ? '' : (window.location.href.split('/')[5]);
      await  vm.GetStatusList();
       await vm.FetchData();        
        vm.privilegeParams = `controller=AssetCatalog&action=`;
        if (vm.catalogId != null && vm.catalogId != '') {
            vm.leftSearchFields = '';
            vm.listheaderbuttons = '';
            vm.buttons[0].isVisible=true;
            vm.buttons[1].isVisible=true;  
        }
        else {
            Schema.bindleftCommonSearchdropdown(vm, "Asset", "Location");
            Schema.bindleftCommonSearchdropdown(vm, "Asset", "Tag");
            Schema.bindleftCommonSearchdropdown(vm, "Asset", "Status");
        }     
        
    },
    updated(){
        $('#Removespan').find('span:first').remove();
    },
    
    methods:
    {  
        async GetPageSize(){            
               var vm =this;
                var params='key=DefaultPageSize'
                await DataService.GetConfigurationValueForCompanyByKey(params).then((response) => {                  
                if(response.data!=null){                                    
                    vm.PageSize = parseInt(response.data);
                }                   
            });
        }, 
       
        changeStatus(rid,statusId)
        {   
                  
            var vm=this;
            vm.confirmR(vm.$t("Are you sure you want to change the status?"), true, (vm.$t("Update") + "  " + vm.$t("Group_Item")), function () {   
                    
                    var url =`rId=${rid}&statusId=${statusId}`
                    DataService.UpdateCatalogStatus(url).then(response => {                  
                    if(response.data.status==true)
                    {
                        vm.ShowAlert(vm.$t('Related Information has been successfully updated.',), "success", true, vm.$t("Alert"));
                        vm.FetchData();
                    }  
                    else
                    {
                        vm.ShowAlert(vm.$t('An error has occurred while updating Related Information. Please retry.'), "success", true, vm.$t("Alert"));   
                    }
                    vm.isLoading=false;
                })                
            },function () {           
              vm.cancelAction();
            })     
            
        },
        cancelAction(){            
            this.FetchData();
        },
        GetStatusList() {
            var vm=this;
            vm.isLoading=true;
            var url =`langCode=en&statusType=AssetManagement&hasglobal=true`
            DataService.GetStatusList(url).then(response => {
                vm.StatusListData=JSON.parse(response.data) ;               
                vm.isLoading=false;
            })
        },
        DownloadAllBarcodePrint()
        {
            //debugger;
            this.PrintAllBarcode();
            // var vm=this;
            // var url = `catalogId=${vm.catalogId}&relatedinfoIds=`;            
            // DataService.DownloadAllBarcode(url).then(response => {               
                
            // })
        } ,    
        onCancel()
        {
            var vm=this;
            vm.$parent.btnCancel();
        },
        onNextClick()
        {
            var vm=this;
            vm.$parent.btnNextTab();
        },
        renderRowActionMethod(){},       
        onPreviousClick()
        {
            var vm=this;
            vm.$parent.btnPreviousTab();
        },
        leftCommonSearch: function (value) {            
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        renderActions(){},
        commonHeaderButtonClick: function (event) {    
            switch (event.callbackfunction) {
                
                    case "OpenDeployProduct":
                    this.DeployProduct();
                    break;                
                    case "OpenModelAddProduct":
                    this.OpenModelAddProduct();
                    break;                
                    case "DownloadAllBarcode":
                    this.DownloadAllBarcode()   
                    break;
                    case "PrintAllBarcode":
                        this.PrintAllBarcode()
                     break
                     case "BackToProductList":
                        this.BackToProductList()
                        break
            }
        },
        BackToProductList()
        {
            var vm=this;
            vm.$router.push({name: 'ProductListing'})  
        },
        OpenModelAddProduct() {            
            var vm=this;
            vm.FromPage='RelatedInfo'
            vm.ShowAddProduct=true;
            // $("#modelAddProduct").modal('show');
            // setTimeout(function () {
            //     $("#modelAddProduct").modal('show');
            //     setTimeout(function () {
            //         vm.SetBootstraptoolTip();
            //     }, 200);
            // }, 1000);
        },    
        SearchByuniqueNameCode()
        {
            var vm=this;
            vm.searchBy=this.txtUniqueNameCode == '' ? '' : this.txtUniqueNameCode;
            vm.FetchData();
        },    
        ClearSearch()
        {         
            var vm=this;
            vm.searchBy='';
            vm.txtUniqueNameCode='';
            vm.FetchData();
        },
       async FetchData() {
          
            var vm = this;
             vm.isLoadings=true;            
            //  if(vm.searchBy!="" || vm.searchBy !=undefined || vm.txtUniqueNameCode !='')
            if(vm.searchCondition == '' || vm.searchCondition== undefined)
             {
                var prams = `search=${vm.searchBy == '' ? '' : vm.txtUniqueNameCode}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=&sortExp=&catalogId=${vm.catalogId}&statusIds=&location_id=&statusId=&typeIds=`;
             }
             else
             {
                 var prams = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&catalogId=${vm.catalogId}&${vm.searchCondition}`;
             }
          await  DataService.ShowRelatedInformationListing(prams).then(response => {    
                if (response.data != null) {           
                    if (response.data.length > 2) {
                        vm.RelatedInformationData = JSON.parse(response.data);
                        vm.TotalRecords = vm.RelatedInformationData[0].TOTAL;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;
                         vm.isLoadings=false;
                         
                    }
                    else {
                        vm.noRecord = true;
                        vm.RelatedInformationData = [];
                        vm.TotalRecords = 0;
                        vm.isLoadings=false;
                        
                    }
                }
                else {
                     vm.isLoadings=false;
                }
                setTimeout(function () {
                vm.CheckBoxBootstrap();
                }, 100);
                setTimeout(function () {
                    vm.ResponsiveDataTable('tablelistingdata')
                }, 500);
            });
        }, 
        sortdata: function (s) {
          var SortDir = "sort tb_headerSortDown";
           this.SortBy = s;
                if (s === this.SortBy) {
                    if (this.SortExp == "") {
                        this.SortExp = "ASC";
                        SortDir = "sort tb_headerSortDown";
                    }
                    else if (this.SortExp == "ASC") {
                        this.SortExp = "DESC";
                        SortDir = "sort tb_headerSortUp";
                    }
                    else
                    {
                        this.SortExp = "ASC";
                        SortDir = "sort tb_headerSortDown";
                    }
                }               
                this.FetchData();
                $("#" + s+'  span:first-child').attr("class", SortDir);
