        formatDate(dateString) {
            
            if (dateString) {
                const date = new Date(dateString);
                if (!isNaN(date.getTime())) {
                // Customize the date format using the appropriate format string
                const formatted = date.toLocaleDateString('en-US', {
                    month: 'numeric',
                    day: 'numeric',
                    year: 'numeric',
                    hour: 'numeric',
                    minute: 'numeric',
                    hour12: true,
                });
                return formatted;
                }
            }
            return '';
        },
